@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
.side{position:absolute;width:20rem;height:20rem;background:linear-gradient(rgba(103,80,255,0.9), rgba(255,80,80,0.9));border-radius:7px;display:flex;justify-content:center;align-items:center;font-size:8rem;color:rgba(255,255,255,0.988)}.scene{width:200px;height:200px;perspective:600px}.cube{width:100%;height:100%;position:relative;transform-style:preserve-3d;transform:translateZ(-100px);transition:all ease-in-out 0.5s}.front{transform:rotateY(0deg) translateZ(100px)}.right{transform:rotateY(90deg) translateZ(100px)}.back{transform:rotateY(180deg) translateZ(100px)}.left{transform:rotateY(-90deg) translateZ(100px)}.top{transform:rotateX(90deg) translateZ(100px)}.bottom{transform:rotateX(-90deg) translateZ(100px)}.cube.show-front{transform:translateZ(-100px) rotateY(0deg)}.cube.show-back{transform:translateZ(-100px) rotateY(-180deg)}.cube.show-right{transform:translateZ(-100px) rotateY(-90deg)}.cube.show-left{transform:translateZ(-150px) rotateY(90deg)}.cube.show-top{transform:translateZ(-100px) rotateX(-90deg)}.cube.show-bottom{transform:translateZ(-100px) rotateX(90deg)}

.controls-container{color:white}.sliders-container{position:fixed;right:1rem;display:flex;flex-direction:column;top:10rem;transform:rotateZ(90deg) scale(1.7)}.flex{display:flex}.flex input{margin-left:0.4rem}

*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html,body{-ms-scroll-chaining:none;overscroll-behavior:none}html{font-size:62.5%;background-image:url(/static/media/blacksand.07d51a30.jpg)}body{font-family:'Lato', sans-serif;font-weight:400;font-size:14px;line-height:1.7;box-sizing:border-box;background:rgba(245,245,245,0)}.app-container{width:100%;padding-top:30rem;perspective:600px}.center{width:17rem;margin-left:50%;transform:translateX(-50%)}.background-blur{width:100vw;height:100vh;position:absolute;background:radial-gradient(rgba(0,0,0,0), rgba(0,0,0,0.896) 80%);-webkit-backdrop-filter:blur(0.4rem);backdrop-filter:blur(0.4rem)}

